<style>

.room {
  margin-left: 10px;
  margin-top: 10px;
  font-size: 16px;
}


.piece {
  margin: 2px 5px;
  padding: 0 15px;
  border: 1px dashed #cdc8eb;
}

.piece:hover {
  cursor: pointer;
}
.page-side {
  margin-left: 20px;
}
.page-card {
  margin-bottom: 10px;
}

</style>
<template>
  <div>
    <div class="spin layout horizontal center">
      <Spin v-if="loading"></Spin>
      <Spin size="large" v-if="loading"></Spin>
      <Spin v-if="loading"></Spin>
    </div>

    <Tabs type="card">
    <Tab-pane label="内务检查" name="name1">

      <h2 class="page-title">
        一周内务检查列表
        <Button @click="refresh" type="text" size="small">
          <Icon type="refresh"></Icon>
          刷新
        </Button>
      </h2>
      <div class="section">
        <div class="page-toolbar">
          <label for="">年级：</label>
          <Select style="width:100px" v-model="showGrade">
            <Option v-for="(item,index) in gradeList" :value="item" :key="index">{{ item }}</Option>
          </Select>

          <label for="">周次：</label>
          <Select style="width:100px" v-model="showWeek">
            <Option v-for="(item,index) in weekList" :value="item" :key="index">{{ item }}</Option>
          </Select>

          <label for="">院子：</label>
          <Select style="width:100px" v-model="showGarden">
            <Option v-for="(item,index) in gardenList" :value="item" :key="index">{{ item }}</Option>
          </Select>

          <label for="">性别：</label>
          <Select style="width:100px" v-model="showSex">
            <Option v-for="(item,index) in sexList" :value="item" :key="index">{{ item }}</Option>
          </Select>

          <Button @click="query">
            <Icon type="refresh"></Icon>
            查询
          </Button>

        </div>
      </div>
      <div class="section">相关结果共 {{ dailysData.length }} 条。</div>
      <div class="section">
        <Button @click="exportReport">
          <Icon type="android-download"></Icon>
          导出内务卫生情况表
        </Button>
        <Button @click="exportNotice">
          <Icon type="android-download"></Icon>
          导出内务卫生通报表
        </Button>

      </div>

      <div class="section">
        <div class="layout horizontal">
          <Table class="flex-6" ref="table" border no-data-text="空" 
      :columns="columns2" :data="dailysData"></Table>
          <div class="page-side flex-1">
            <Card class="page-card">
              <p slot="title">已有检查情况</p>
              <ul>
                <li v-for="(item,index) in existDailys" 
              :key="index">
                  <p>年级：{{ item.grade }}届</p>
                  <p>周次：{{ item.weeks.join('，') }}</p>
                  <p>院子：{{ item.garden.join('，') }}</p>
                  <p>性别：{{ item.sex.join('，') }}</p>
                  <hr></li>

              </ul>
            </Card class="page-card">
          </div>
        </div>
        <br></div>
      <Back-top></Back-top>

    </Tab-pane>

    <Tab-pane label="新增检查" name="name2">
      <h2 class="page-title">
        宿舍列表
        <Button @click="refresh" type="text" size="small">
          <Icon type="refresh"></Icon>
          刷新
        </Button>
      </h2>
      <div class="section">
        <div class="page-toolbar">
          <label for="">年级：</label>
          <Select style="width:100px" v-model="showGrade">
            <Option v-for="(item,index) in gradeList" :value="item" :key="index">{{ item }}</Option>
          </Select>

          <Button @click="query">
            <Icon type="refresh"></Icon>
            查询
          </Button>
        </div>
      </div>

      <div class="section">
        <div class="layout vertical">
          <div v-for="item in roomsData" :key="item">
            <h2 class="page-title">{{ item._id.garden }}</h2>
            <div class="layout horizontal" v-for="floor in item.rooms">
              <Button v-for="r in floor" :key="r" @click="showRoomModal(r)" class="room">{{ r.roomnumber }}</Button>
            </div>
          </div>
        </div>
      </div>

      <Modal v-model="newRecordModal" width="1000">
        <h2 slot="header">{{ roomTitle }}</h2>
        <div class="layout horizontal">
          <div class="section">
            <Form :label-width="80" label-position="left">
              <Form-item label="周次：">

                <Input-number :max="30" :min="1" v-model="newRecord.week"></Input-number>

            </Form-item>
            <Form-item  label="日期：">
              <Date-picker readonly v-model="newRecord.date" type="date" 
    style="width: 220px"></Date-picker>

            </Form-item>
            <Form-item>
              <Date-picker
        :open="true"
        v-model="newRecord.date"
        type="date">
                <div></div>

              </Date-picker>
            </Form-item>
          </Form>
        </div>
        <div class="section">
          <Form :label-width="80">
            <Form-item label="问题描述：">
              <div class="layout vertical">
                <Input v-model="newRecord.desc" style="width:220px;" type="textarea" :autosize="{minRows: 3, maxRows: 6}" placeholder="请输入..."></Input>
              <Button type="dashed" size="small" @click="clearDesc"
       style="width:50px;margin-top:5px;">
                <Icon type="arrow-left-c"></Icon>
              </Button>

              <dl>
                <dd>
                  分割线
                  <span @click="addDesc" class="piece">|</span>
                </dd>
                <dt>床号</dt>
                <dd class="layout horizontal wrap">
                  <span class="piece" v-for="b in issueDesc.bed" @click="addDesc">{{ b }}</span>
                </dd>
                <dt>位置</dt>
                <dd class="layout horizontal wrap">
                  <span class="piece" v-for="p in issueDesc.pos" @click="addDesc">{{ p }}</span>
                </dd>
                <dt>物品</dt>
                <dd class="layout horizontal wrap">
                  <span class="piece" v-for="g in issueDesc.good" @click="addDesc">{{ g }}</span>
                </dd>
                <dt>问题</dt>
                <dd class="layout horizontal wrap">
                  <span class="piece" v-for="i in issueDesc.issue" @click="addDesc">{{ i }}</span>
                </dd>
              </dl>

            </div>
          </Form-item>
        </Form>
      </div>
    </div>
    <div slot="footer">
      <div class="action layout horizontal">
        <div class="flex"></div>
        <Button @click="cancel">取消</Button>
        <Button type="primary" @click="saveRecord">确定</Button>
      </div>
    </div>
  </Modal>
</Tab-pane>

</Tabs>

</div>

</template>
<script>
'use strict'

import config from '../config'
import utils from '../utils'
import axios from 'axios'

const prefix = config.service.apiUrl

let getFloor = function (room) {
  room['floor'] = room.roomnumber.substring(0, 1)
  return room
}

export default {
  data () {
    return {
      loading: false,
      existDailys: [],
      issueDesc: {
        bed: [
          '1床', '2床', '3床', '4床', '5床', '6床', '7床', '8床', '空床'
        ],
        pos: [
          '床下', '桌面', '地面', '阳台', '窗台', '洗漱台', '脸盆', '厕坑', '墙'
        ],
        good: [
          '床单', '被子', '洗漱用品', '鞋子', '毛巾'
        ],
        issue: [
          '皱', '不规范', '乱放', '脏', '有灰'
        ]
      },
      gardenList: [
        '雅苑', '和苑', '馨苑'
      ],
      sexList: [
        '男生', '女生'
      ],
      gradeList: [],
      weekList: [],
      newRecordModal: false,
      currentRoom: {},
      roomsData: [],
      newRecord: {
        date: new Date(),
        week: 1,
        garden: '',
        roomnumber: '',
        leader: '',
        sex: '',
        desc: '',
        class: '',
        grade: '',
        teacher: ''
      },
      columns2: [{
        type: 'index',
        // width: 80,
        title: '序号'

      }, {
        title: '日期',
        width: 100,
        key: 'date'
      }, {
        title: '院子',
        key: 'garden'
      }, {
        title: '房间',
        key: 'roomnumber'
      }, {
        title: '舍长',
        key: 'leader'
      }, {
        title: '问题描述',
        key: 'desc',
        width: 200
      }, {
        title: '年级',
        key: 'grade'
      }, {
        title: '班级',
        key: 'class'
      }, {
        title: '班主任',
        key: 'teacher'
      }, {
        title: '操作',
        key: 'action',

        width: 120,
        render: (h, params) => {
          return h('div', [
            this.delConfirmRender(h, params.row)
          ])
        }
      }],
      dailysData: [],
      showWeek: 0,
      showGarden: '雅苑',
      showGrade: '',
      showSex: '女生'
    }
  },
  created: function () {
    this.fetch()
  },
  computed: {
    roomTitle: function () {
      const roomTitle = this.currentRoom.garden + '-' + this.currentRoom.sex + '-' +
      this.currentRoom.roomnumber + '-' + this.currentRoom.leader + '-' +
      this.currentRoom.grade + '届-' + this.currentRoom.class + '班'
      return roomTitle
    }
  },

  methods: {
    refresh () {
      this.fetch()
    },
    clearDesc () {
      this.newRecord.desc = ''
    },
    addDesc (a) {
      let span = a.target
      this.newRecord.desc += span.childNodes[0].data
    },
    fetchGradesDailys () {
      const that = this
      const url = prefix + '/utils/gradesdailys'

      axios.get(url)
        .then(function (res) {
          that.existDailys = res.data
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    fetch () {
      this.$Loading.start()
      this.fetchGrades().then(() => {
        this.fetchRoomsData()
        this.fetchWeeks().then(() => {
          this.fetchDailysData()
        })
        this.fetchGradesDailys()
        this.$Loading.finish()
      })
    },
    fetchGrades () {
      return new Promise((resolve, reject) => {
        const that = this
        const url = prefix + '/utils/grades'

        axios.get(url)
          .then(function (res) {
            that.gradeList = res.data[0].grades.slice().sort(utils.sortNumber)
            that.showGrade = that.gradeList[0]
            resolve(that.showGrade)
          })
          .catch(function (err) {
            reject(err)
            console.log(err)
          })
      })
    },
    fetchWeeks () {
      return new Promise((resolve, reject) => {
        const url = prefix + '/utils/weeks'
        const that = this
        axios.get(url)
          .then(function (res) {
            that.weekList = res.data[0].weeks.slice().sort(utils.sortNumber)
            that.showWeek = that.weekList[0]
            resolve(that.showWeek)
          })
          .catch(function (err) {
            reject(err)
            console.log(err)
          })
      })
    },
    delConfirmRender (h, row) {
      let btn = h('Button', {
        props: {
          type: 'text',
          size: 'small'
        },
        style: {
          fontSize: '14px'
        }
      }, '删除')
      let okBtn = h('Button', {
        props: {
          type: 'warning',
          size: 'small'
        },
        style: {
          marginTop: '10px'
        },
        on: {
          click: () => {
            this.delDaily(row)
          }
        }
      }, '删除')
      let btnLine = h('div', {
        class: 'layout horizontal'
      }, [h('div', {class: 'flex'}, ''), okBtn])
      let titleStr = row.garden + '-' + row.roomnumber + ' 宿舍 ' + row.date
      let title = h('div', {
        slot: 'title'
      }, '确认删除？')
      let tipStr = h('div', {
        style: {
          fontSize: '14px'
        }
      }, titleStr + '的检查记录将会被删除。')
      let warningStr = h('div', {
        style: {
          color: '#ff0000',
          fontSize: '14px'
        }
      }, '警告：此操作不可恢复！')
      let content = h('div', {
        slot: 'content'
      }, [tipStr, warningStr, btnLine])

      let pop = h('Poptip', {
        props: {
        }
      }, [btn, title, content])
      return pop
    },
    delDaily (row) {
      const url = prefix + '/daily/' + row._id
      const that = this
      axios.delete(url)
        .then(function (res) {
          that.fetchDailysData()
          that.$Message.info('删除一条内务检查记录。')
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    query () {
      this.loading = true
      this.fetchDailysData()
      this.fetchRoomsData()
      setTimeout(() => {
        this.loading = false
      }, 1000)
    },
    exportData () {
      this.$refs.table.exportCsv({
        filename: '原始数据'
      })
    },
    saveRecord () {
      const url = prefix + '/daily'

      this.newRecord.garden = this.currentRoom.garden
      this.newRecord.roomnumber = this.currentRoom.roomnumber
      this.newRecord.leader = this.currentRoom.leader
      this.newRecord.class = this.currentRoom.class
      this.newRecord.teacher = this.currentRoom.teacher
      this.newRecord.grade = this.currentRoom.grade
      this.newRecord.sex = this.currentRoom.sex

      const that = this
      axios.post(url, this.newRecord)
        .then(function (res) {
          that.newRecordModal = false
          that.$Message.info('新增一条检查记录。')
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    cancel () {
      this.newRecordModal = false
    },
    showRoomModal: function (room) {
      this.newRecordModal = true
      this.newRecord.week = this.showWeek
      this.currentRoom = room
    },
    fetchRoomsData: function () {
      const url = prefix + '/rooms?grade=' + this.showGrade
      const that = this
      axios.get(url)
        .then(function (res) {
          let rooms = res.data.slice()
          rooms.forEach((a) => {
            let floors = {
              '1': [],
              '2': [],
              '3': [],
              '4': [],
              '5': [],
              '6': []
            }
            let tf = a.rooms.map(getFloor)

            tf.forEach((dd) => {
              floors[dd.floor].push(dd)
            })
            for (let ff in floors) {
              if (floors[ff].length > 0) {
                floors[ff].sort(utils.sortRoomNumber)
              }
            }
            a.rooms = floors
          })

          that.roomsData = rooms
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    fetchDailysData () {
      const url = prefix + '/daily?week=' + this.showWeek + '&garden=' + this.showGarden + '&grade=' + this.showGrade + '&sex=' + this.showSex

      const that = this
      axios.get(url)
        .then(function (res) {
          let dailys = res.data.slice()
          dailys.forEach(function (a, b, c) {
            c[b].date = a.date.substring(0, 10)
          })
          that.dailysData = dailys
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    exportReport () {
      const url = prefix + '/daily/report?week=' + this.showWeek + '&garden=' + this.showGarden + '&grade=' + this.showGrade + '&sex=' + this.showSex
      axios.get(url)
        .then(function (res) {
          let fl = res.data
          let arr = fl.split('/')
          let link = config.service.host + '/archive/' + arr[arr.length - 1]
          window.location.href = link
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    exportNotice () {
      const url = prefix + '/daily/notice?week=' + this.showWeek +
        '&grade=' + this.showGrade
      axios.get(url)
        .then(function (res) {
          let fl = res.data
          let arr = fl.split('/')
          let link = config.service.host + '/archive/' + arr[arr.length - 1]
          window.location.href = link
        })
        .catch(function (err) {
          console.log(err)
        })
    }
  }
}

</script>



