'use strict'

/*
测试用后台的地址，生产环境自动设置为空
 */
var host = 'http://127.0.0.1:3000'

if (process.env.NODE_ENV === 'production') {
  host = ''
}

var config = {
  service: {
    host,
    apiUrl: `${host}/api/sanitation`
  }
}

module.exports = config




